<template>
  <div class="search">

    <!-- 页面顶部 -->
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <!-- 搜索栏 -->
    <van-search v-model="searchData" show-action placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div @click='goSearch(searchData)'>搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="searchHistory.length > 0">
      <div class="title">
        <span>最近搜索</span>
        <van-icon @click="clearHistory" name="delete-o" size="16" />
      </div>
      <div class="list">
        <div v-for="item in searchHistory" :key='item' class="list-item" @click="goSearch(item)">{{ item }}</div>
      </div>
    </div>
  </div>
 </template>

<script>
import { getHistoryList, setHistoryList } from '@/utils/storage'

export default {
  name: 'SearchIndex',
  data () {
    return {
      searchData: '', // 输入的搜索数据
      searchHistory: getHistoryList() // 搜索历史记录
    }
  },
  methods: {

    /* 搜索 */
    goSearch (keyValue) {
      // 获取关键词下标
      const index = this.searchHistory.indexOf(keyValue)
      // 判断搜索历史中是否已存在关键词
      if (index !== -1) {
        // 删除原有关键词
        this.searchHistory.splice(index, 1)
      }
      // 将关键词添加至表头
      this.searchHistory.unshift(keyValue)
      // 持久化存储
      setHistoryList(this.searchHistory)
      // 跳转
      this.$router.push(`/searchlist?search=${keyValue}`)
    },

    /* 清空搜索历史 */
    clearHistory () {
      this.searchHistory = []
      // 持久化存储
      setHistoryList(this.searchHistory)
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
